	body{
		overflow-x:hidden;
	}
	.head_menu_block{
		position: relative;
		z-index:5;
		top:2vw;
		width:79vw;
		left: 16vw;
		height:8vw;
	}
	.logos{
		height:100%;
	}
	.menu{
		position: relative;
		z-index:5;
		width:75%;
		left: 12%;
		height:8vw;
		margin-top:-8vw;
	}
	.menu_nav{
		width:100%;
	}
	.menu_link {
	  text-decoration: none;
	}
	.menu_ul{
		list-style: none;
		margin: 0;
		padding-left: 0;
	}
	.menu_li {
		color: #fff;
		background: darkorange;
		display: block;
		float: left;
		padding: 1.4vw;
		font-size:1.3vw;
		position: relative;
		text-decoration: none;
		transition-duration: 0.5s;
		width:8vw;
		margin-top:2vw;
		text-align:center;
		font-size:1.2vw;
		font-family: 'Open Sans', sans-serif;
	}
	.menu_li a {
	  color: #fff;
	}
	.menu_li:hover {
		background: red;
		cursor: pointer;
	}
	.menu_ul li ul {
		background: orange;
		visibility: hidden;
		opacity: 0;
		width: 10.8vw;
		position: absolute;
		transition: all 0.5s ease;
		margin-top: 1.4vw;
		left: 0;
		display: none;
		font-family: 'Open Sans', sans-serif;
	}
	.menu_ul li:hover > ul,
	.menu_ul li ul:hover {
		visibility: visible;
		opacity: 1;
		display: block;
	}

	.menu_ul li ul li {
		clear: both;
		width: 8vw;
		margin-top: 0vw;
	}
	.social{
		position: relative;
		z-index:5;
		width:10%;
		margin-left: 85%;
		height:8vw;
		margin-top:-8vw;
	}
	.social_logo_div{
		width:4vw;
		margin-top:2.1vw;
	}
	.social_logo{
		width:4vw;
	}
	.galleryBlock{
		position:relative;
		margin-top:1vw;
		width:97%;
		margin-left:2vw;
		overflow-x:hidden;
	}
	.gallery_item{
		position:relative;
		margin-top:3vw;
		width:97%;
		margin-left:6vw;
		overflow-x:hidden;
		overflow-y:visible;
		font-size:2vw;
		font-family: 'Open Sans', sans-serif;
	}